<template>
  <div class="calculator">
    <div class="display">{{ current || 0 }}</div>
    <div @click="clear" class="btn">C</div>
    <div @click="sign" class="btn">+/-</div>
    <div @click="percent" class="btn">%</div>
    <div @click="divide" class="btn operator">÷</div>
    <div @click="append('7')" class="btn">7</div>
    <div @click="append('8')" class="btn">8</div>
    <div @click="append('9')" class="btn">9</div>
    <div @click="times" class="btn operator">x</div>
    <div @click="append('4')" class="btn">4</div>
    <div @click="append('5')" class="btn">5</div>
    <div @click="append('6')" class="btn">6</div>
    <div @click="minus" class="btn operator">-</div>
    <div @click="append('1')" class="btn">1</div>
    <div @click="append('2')" class="btn">2</div>
    <div @click="append('3')" class="btn">3</div>
    <div @click="add" class="btn operator">+</div>
    <div @click="append('0')" class="btn zero">0</div>
    <div @click="dot" class="btn">.</div>
    <div @click="equal" class="btn operator">=</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      previous: '',
      current: '',
      operator: null,
      operatorClicked: false
    };
  },
  methods: {
    clear() {
      this.current = '';
      this.previous = null;
    },
    sign() {
      this.current =
        this.current.charAt(0) === '-' //一文字目が「-」の場合
          ? this.current.slice(1)
          : `-${this.current}`;
    },
    percent() {
      // previuseがあれば % で計算
      this.previous !== null
        ? (this.current = `${parseFloat(this.previous) *
            parseFloat(this.current / 100)}`)
        : (this.current = `${parseFloat(this.current) / 100}`);
    },
    append(number) {
      if (this.operatorClicked) {
        this.current = '';
        this.operatorClicked = false;
      }
      if (this.current.length < 8) this.current = `${this.current}${number}`;
    },
    dot() {
      if (!this.current.includes('.')) {
        this.append('.');
      }
    },
    setPrevious() {
      this.previous = this.current;
      this.operatorClicked = true;
    },
    divide() {
      this.operator = (a, b) => a / b; // operatorに割り算関数を代入
      this.setPrevious();
    },
    times() {
      this.operator = (a, b) => a * b; // operatorに足し算関数を代入
      this.setPrevious();
      console.log(this.operatorClicked);
    },
    minus() {
      this.operator = (a, b) => a - b; // operatorに足し算関数を代入
      this.setPrevious();
    },
    add() {
      this.operator = (a, b) => a + b; // operatorに足し算関数を代入
      this.setPrevious();
    },
    equal() {
      this.current = `${this.operator(
        // 計算実行
        parseFloat(this.previous),
        parseFloat(this.current)
      )}`;
      this.previous = null;
    }
  }
};
</script>

<style lang="scss" scoped>
