  width: 300px;
  margin: 120px auto 0;
  font-size: 40px;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(65px, auto);
  box-shadow: 0 0 20px #999;

  .display {
    grid-column: 1 / 5;
    background-color: #333;
    color: #fff;
    text-align: right;
    padding: 15px;
    font-size: 40px;
  }

  .zero {
    grid-column: 1 / 3;
  }

  .btn {
    background-color: #f2f2f2;
    border: 1px solid #999;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    &:active {
      opacity: 0.5;
    }
  }

  .operator {
    background-color: orange;
    color: #fff;
  }
}
</style>
